.course {
  .ant-collapse {
    background: #fff; }
  .ant-timeline {
    padding: 20px 6% 0; } }

.course-pannel {
  position: relative;
  .course-pannel-title {
    position: absolute;
    left: -26px;
    width: 164px;
    height: 60px;
    line-height: 48px;
    padding: 0 32px 9px 20px;
    color: #fff;
    font-size: 20px;
    background: url(../assets/img/label-highlight.svg) 50% no-repeat; }
  .course-detail-wrap {
    padding: 80px 6% 48px;
    .course-detail-title {
      font-size: 20px; } }
  .course-icon-wrap {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    text-align: center; } }
