.course{
  background: url(../assets/img/course/course-bg.svg) 0 10% repeat-y;
  background-size: cover;
  .titleBlock{
    position: relative;
    height: 300px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    .titleWrap{
      width: 1170px;
      margin: 0 auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      // text-align: center;
      h1,h2,h3,p{
        color: #fff;
      }
    }
  }
  .titleLeft{
    :global(.title-wrap){
      margin-bottom: 12px;
      text-align: left;
      :global(.dash){
        margin: 0;
      }
    }
  }
  .courseBlock{
    padding: 48px 10%;
    :global(.title-wrap){
      margin-bottom: 12px;
    }
  }
  .courseTextContent{
    margin: 12px 0;
  }

  :global(.course){
    margin-bottom: 20px;
  }

  .priceBlock{
    height: 120px;
    line-height: 120px;
    font-size: 20px;
    text-align: center;
    color: #fff;
    background: url(../assets/img/course/price_bg.png) center center no-repeat;
    background-size: cover;
  }
}